import React, {Component} from 'react';
import Cell from './Cell';

export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
            mapActions: props.mapActions,
        };

    }


    makeGrid(){
        let grid = [];
        let key = 0;

        for(let i = 0;i < this.state.grid.length;i++){
            for(let j = 0;j < this.state.grid[0].length;j++){
                key++;
                grid.push(
                    <Cell
                        key = {key}
                        serialNumber = {this.state.grid[i][j]}
                        x = {i}
                        y = {j}
                        mapAction = {this.props.mapActions}
                    />
                );
            }
        }


        return grid;
    }

    clickHandle(){

        console.log(this.state.mapActions);
    }

    render(){
        let grid = this.makeGrid();
        return(
            <div className="grid" onClick={this.clickHandle.bind(this)}>
                {
                   grid
                }
            </div>
        );
    }
}